import React from 'react';
import Paginator from "../../common/Paginator/Paginator";
import User from "./User/User";
import {UserType} from "../../types/types";

type PropsType = {
    totalUsersCount: number
    pageSize: number
    currentPage: number
    onChangePage: (pageNumber: number) => void
    users: Array<UserType>
    portionSize: number
    follow: (userId: number) => void
    followInProgress: Array<number>
    unfollow: (userId: number) => void
}

let Users: React.FC<PropsType> = ({totalUsersCount, pageSize,
                 currentPage, onChangePage,
                 users, portionSize, follow,
                 followInProgress, unfollow }) => {

    return <div>
        <Paginator totalItemsCount={totalUsersCount} currentPage={currentPage}
                   onChangePage={onChangePage} pageSize={pageSize} portionSize={portionSize}/>
        <div>
            {
                users.map(u =>  <User follow={follow}
                                     followInProgress={followInProgress}
                                     unfollow={unfollow} user={u}
                                      key={u.id}
                    />
                )
            }
        </div>
    </div>
};

export default Users;